rder: 2px solid #F00;\n    }\n    .image.team-blue {\n      border: 2px solid rgb(0, 132, 255);\n    }\n    .base-infos {\n      width: 80%;\n      display: block;\n      .health {\n        margin-top: 5px;\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        align-items: center;\n        position: relative;\n        height: 50px;\n        width: 60px;\n        img.hp-full {\n          height: 50px;\n          width: 60px;\n          position: absolute;\n          top: 0;\n          left: 0;\n          z-index: 0;\n        }\n        img.hp-empty {\n          height: 50px;\n          width: 60px;\n          position: absolute;\n          top: 0;\n          left: 0;\n          object-fit: cover;\n          object-position: top;\n          z-index: 1;\n        }\n        span {\n          text-shadow: 1px 1px 0 black;\n          display: block;\n          z-index: 2;\n        }\n      }\n    }\n  }\n  .offensive-stats {\n    padding-left: 5px;\n    padding-right: 5px;\n    li {\n      height: 25px;\n      margin-bottom: 3px;\n      display: flex;\n      img {\n        margin-right: 5px;\n        width: 25px;\n      }\n      img[src=\"\"] {\n        visibility: hidden;\n      }\n      span {\n        line-height: 25px;\n        flex: 3;\n      }\n      input {\n        min-width: 0;\n        border: none;\n        flex: 3;\n        appearance: textfield;\n        background-color: #FFF6;\n        border-radius: 2px;\n        padding-left: 5px;\n        color: #EEE;\n      }\n      input:disabled {\n        background-color: #0000;\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
